(function () {
  'use strict';

  var IMAGES_ROOT = 'assets/img/';

  angular.module('BlurAdmin.theme')
    .constant('layoutSizes', {
      resWidthCollapseSidebar: 1200,
      resWidthHideSidebar: 500
    })
    .constant('layoutPaths', {
      images: {
        root: IMAGES_ROOT,
        profile: IMAGES_ROOT + 'app/profile/',
        amMap: 'assets/img/theme/vendor/ammap//dist/ammap/images/',
        amChart: 'assets/img/theme/vendor/amcharts/dist/amcharts/images/'
      }
    })
    .constant('colorHelper', {
      tint: function(color, weight) {
        return mix('#ffffff', color, weight);
      },
      shade: function(color, weight) {
        return mix('#000000', color, weight);
      },
    })
    .constant('metrics', {
      executive: [
          {
        	'field': 'revenue',
        	'metric': 'Revenue',
        	'format': 'formatMoney',
        	'precision': 0,
        	'misc': '$'
        }, {
        	'field': 'ebitPercent',
        	'metric': 'EBIT %',
        	'format': 'formatNumber',
        	'precision': 2,
        	'misc': '%'
        }, {
        	'field': 'iph',
        	'metric': 'IPH',
        	'format': 'formatNumber',
        	'precision': 0,
        	'misc': ''
        }, {
        	'field': 'sla',
        	'metric': 'SLA Attainment',
        	'format': 'formatNumber',
        	'precision': 0,
        	'misc': '%'
        }, {
        	'field': 'costPerTkt',
        	'metric': 'Cost p/Incident',
        	'format': 'formatMoney',
        	'precision': 2,
        	'misc': '$'
        }, {
        	'field': 'ebitPerTkt',
        	'metric': 'EBIT p/Incident',
        	'format': 'formatMoney',
        	'precision': 2,
        	'misc': '$'
        }, {
        	'field': 'revPerHead',
        	'metric': 'Revenue p/Head',
        	'format': 'formatMoney',
        	'precision': 0,
        	'misc': '$'
        }
      ]
    });

  function shade(color, weight) {
    return mix('#000000', color, weight);
  }

  function tint(color, weight) {
    return mix('#ffffff', color, weight);
  }

  //SASS mix function
  function mix(color1, color2, weight) {
    // convert a decimal value to hex
    function d2h(d) {
      return d.toString(16);
    }
    // convert a hex value to decimal
    function h2d(h) {
      return parseInt(h, 16);
    }

    var result = "#";
    for(var i = 1; i < 7; i += 2) {
      var color1Part = h2d(color1.substr(i, 2));
      var color2Part = h2d(color2.substr(i, 2));
      var resultPart = d2h(Math.floor(color2Part + (color1Part - color2Part) * (weight / 100.0)));
      result += ('0' + resultPart).slice(-2);
    }
    return result;
  }
})();

