/**
 * Change top "Daily Downloads", "Active Users" values with animation effect
 */
(function () {
  'use strict';

  angular.module('BlurAdmin.theme')
      .directive('animatedChange', animatedChange);

  /** @ngInject */
  function animatedChange($timeout) {
    return {
      link: function (scope, element) {
        $timeout(function () {
          var newValue = element.attr('new-value');
          var oldvalue = parseInt(element.html());

          function changeValue(val) {
            $timeout(function () {
              element.html(val);
            }, 30);
          }

          if (newValue > oldvalue) {
            for (var i = oldvalue; i <= newValue; i++) {
              changeValue(i);
            }
          } else {
            for (var j = oldvalue; j >= newValue; j--) {
              changeValue(j);
            }
          }
          $timeout(function () {
            element.next().find('i').addClass('show-arr');
          }, 500);
        }, 3500);
      }
    };
  }

})();
/**
 * Auto expand textarea field
 */
(function () {
  'use strict';

  angular.module('BlurAdmin.theme')
      .directive('autoExpand', autoExpand);

  /** @ngInject */
  function autoExpand() {
    return {
      restrict: 'A',
      link: function ($scope, elem) {
        elem.bind('keydown', function ($event) {
          var element = $event.target;
          $(element).height(0);
          var height = $(element)[0].scrollHeight;
          height = (height < 16) ? 16 : height;
          $(element).height(height);
        });

        // Expand the textarea as soon as it is added to the DOM
        setTimeout(function () {
          var element = elem;
          $(element).height(0);
          var height = $(element)[0].scrollHeight;
          height = (height < 16) ? 16 : height;
          $(element).height(height);
        }, 0)
      }
    };
  }

})();
(function () {
  'use strict';

  angular.module('BlurAdmin.theme')
      .directive('autoFocus', autoFocus);

  /** @ngInject */
  function autoFocus($timeout, $parse) {
    return {
      link: function (scope, element, attrs) {
        var model = $parse(attrs.autoFocus);
        scope.$watch(model, function (value) {
          if (value === true) {
            $timeout(function () {
              element[0].focus();
              element[0].select();
            });
          }
        });
        element.bind('blur', function () {
          scope.$apply(model.assign(scope, false));
        });
      }
    };
  }

})();
(function () {
  'use strict';

  angular.module('BlurAdmin.theme')
      .directive('inpDaterange', btnDaterange);

  /** @ngInject */
  function btnDaterange($timeout, $parse) {
    function BtnDaterangeLink ($scope, element, attrs, ctrl) {
      $scope.initRun = false;
      var ngModelCtrl = ctrl[0];
      var btnCtrl = ctrl[1];
      ngModelCtrl.$viewChangeListeners.push(function() {
        btnCtrl.updateRoot(ngModelCtrl.$modelValue);
      });
      $scope.$watch(function() {
        return ngModelCtrl.$modelValue;
      }, function (nVal, oVal) {
        if (!isNaN(nVal) && !$scope.initRun) {
          btnCtrl.updateRoot(ngModelCtrl.$modelValue);
          $scope.initRun = true;
        }
      }, true);
    }
    function BtnDaterangeCtrl ($scope, $element, $attrs, $compile, $log, $parse, $window, $document, $rootScope) {
      var o = this;
      o.updateRoot = function (ngVal) {
        $rootScope.globals.filterMonth =  ngVal;
      };
    }
    // ngModel.$viewChangeListeners.push(function() {
    //   $scope.date = parseDateString(ngModel.$viewValue);
    // });
    return {
      restrict: 'C',
      require: ['ngModel', 'inpDaterange'],
      controller: BtnDaterangeCtrl,
      controllerAs: 'vmBtnDate',
      link: BtnDaterangeLink
    };
  }

})();

